@font-face {
  font-family: "Gilroy";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Gilroy Regular"),
    local("Gilroy-Regular"),
    url("../fonts/gilroy/Gilroy-Regular.woff2") format("woff2"),
    url("../fonts/gilroy/Gilroy-Regular.woff") format("woff");
}

@font-face {
  font-family: "Gilroy";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: local("Gilroy SemiBold"),
    local("Gilroy-SemiBold"),
    url("../fonts/gilroy/Gilroy-SemiBold.woff2") format("woff2"),
    url("../fonts/gilroy/Gilroy-SemiBold.woff") format("woff");
}

@font-face {
  font-family: "Gilroy";
  font-weight: 800;
  font-style: normal;
  font-display: swap;
  src: local("Gilroy ExtraBold"),
    local("Gilroy-ExtraBold"),
    url("../fonts/gilroy/Gilroy-ExtraBold.woff2") format("woff2"),
    url("../fonts/gilroy/Gilroy-ExtraBold.woff") format("woff");
}

@font-face {
  font-family: "Rubik";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Rubik"),
    local("Rubik-Regular"),
    url("../fonts/rubik/rubik-v9-latin_cyrillic-regular.woff2") format("woff2"),
    url("../fonts/rubik/rubik-v9-latin_cyrillic-regular.woff") format("woff");
}

@font-face {
  font-family: "Rubik";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("Rubik Medium"),
    local("Rubik-Medium"),
    url("../fonts/rubik/rubik-v9-latin_cyrillic-500.woff2") format("woff2"),
    url("../fonts/rubik/rubik-v9-latin_cyrillic-500.woff") format("woff");
}

@font-face {
  font-family: "Open Sans";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Open Sans Regular"),
    local("OpenSans-Regular"),
    url("../fonts/open-sans/open-sans-v17-latin_cyrillic-regular.woff2") format("woff2"),
